/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

html,
body {
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

.site-header {
  background-repeat: no-repeat;
  background-size: cover;
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  padding: 5rem 0;
}
.site-header .logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .site-header .logo {
    margin: 0;
  }
}
.site-header .header-content {
  padding: 10rem 0;
}
@media (min-width: 1024px) {
  .site-header .header-content {
    padding: 15rem 0;
  }
}
.site-header .header-content h1 {
  color: #ffffff;
  font-size: 4.5rem;
}
@media (min-width: 768px) {
  .site-header .header-content h1 {
    font-size: 6rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .site-header .header-content h1 {
    max-width: 40%;
  }
}

.create-grid h2 {
  font-size: 3.3rem;
  display: inline-block;
  margin-top: 3rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .create-grid h2 {
    margin-top: 0;
  }
}
.create-grid h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
@media (min-width: 768px) {
  .create-grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.benefits {
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .benefits {
    padding: 8rem 0;
  }
}
.benefits .benefits-grid {
  align-items: center;
}
@media (min-width: 768px) {
  .benefits .benefits-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
  }
}
.benefits .benefits-content {
  grid-column: 1/4;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .benefits .benefits-content {
    margin-bottom: 0;
  }
}
.benefits .benefits-content h2 {
  font-size: 3.3rem;
  display: inline-block;
  margin-top: 3rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .benefits .benefits-content h2 {
    margin-top: 0;
  }
}
.benefits .benefits-content h2::after {
  content: "";
  background-color: #ffffff;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.benefits .image {
  grid-column: 4/6;
  transition-property: transform;
  transition-duration: 0.3s;
}
.benefits .image:hover {
  transform: rotate(8deg) scale(1.05);
}

.stats {
  padding-bottom: 5rem;
}
.stats h2 {
  font-size: 3.3rem;
  display: inline-block;
  margin-top: 3rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .stats h2 {
    margin-top: 0;
  }
}
.stats h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
@media (min-width: 768px) {
  .stats h2 {
    margin-top: 5rem;
  }
}
.stats p {
  margin-bottom: 3rem;
}

.plans {
  background-color: #313c4e;
}
.plans h2 {
  font-size: 3.3rem;
  display: inline-block;
  margin-top: 3rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .plans h2 {
    margin-top: 0;
  }
}
.plans h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.plans .description {
  color: #ffffff;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .plans .description {
    margin-bottom: 0;
  }
}

.our-plans {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .our-plans {
    flex-direction: row;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.our-plans .plan {
  background-color: #ffffff;
  border: 0.1rem solid #e1e1e1;
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
}
@media (min-width: 768px) {
  .our-plans .plan {
    border: none;
    transition-property: transform;
    transition-duration: 300ms;
  }
  .our-plans .plan:hover {
    transform: scale(1.05);
  }
}
.our-plans .plan .price {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
  text-transform: uppercase;
}
.our-plans .plan .list li {
  margin-bottom: 2rem;
  text-align: center;
}
.our-plans .plan .list li:last-of-type {
  margin-bottom: 0;
}
.our-plans .plan.starter {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.our-plans .plan.business {
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
  background-color: #84d9bc;
  order: -1;
  z-index: 100;
}
@media (min-width: 768px) {
  .our-plans .plan.business {
    order: unset;
    margin-top: -5rem;
    margin-bottom: -5rem;
    border-radius: 2rem;
    background-position: 50% 7rem;
    padding-top: 21rem;
  }
}
.our-plans .plan.pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
  order: -1;
}
@media (min-width: 768px) {
  .our-plans .plan.pro {
    order: unset;
  }
}

@media (min-width: 768px) {
  .testimonials {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
  .testimonials .image {
    grid-column: 1/3;
  }
  .testimonials .content {
    grid-column: 3/6;
  }
}

.content h2 {
  font-size: 3.3rem;
  display: inline-block;
  margin-top: 3rem;
  color: #313c4e;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .content h2 {
    margin-top: 0;
  }
}
.content h2::after {
  content: "";
  background-color: #84d9bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.content .testimonial {
  background-color: #84d9bc;
  color: #000;
  border: 0.5rem solid #313c4e;
  margin-bottom: 3rem;
  padding: 2rem;
  border-radius: 1rem;
  position: relative;
}
@media (min-width: 768px) {
  .content .testimonial {
    max-width: 70%;
  }
  .content .testimonial:nth-child(2) {
    margin-left: 10%;
  }
  .content .testimonial:nth-child(3) {
    margin-left: 20%;
  }
  .content .testimonial:nth-child(4) {
    margin-left: 30%;
  }
}
.content .testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}
.content .testimonial p {
  margin: 0;
  padding-left: 6rem;
}
.content .testimonial:last-of-type {
  margin-bottom: 0;
}

.footer {
  background-color: #313c4e;
  color: #ffffff;
}
.footer .footer-grid {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .footer .footer-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer .logo {
  max-width: 20rem;
  margin: 0 auto 5rem auto;
}
@media (min-width: 768px) {
  .footer .logo {
    margin-bottom: 0;
  }
}
.footer .footer-widget {
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .footer .footer-widget {
    margin-bottom: 0;
  }
}
.footer .footer-widget h3 {
  color: #84d9bc;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .footer .footer-widget h3 {
    text-align: left;
  }
}
.footer .footer-widget nav {
  display: flex;
  flex-direction: column;
}
.footer .footer-widget a {
  color: #ffffff;
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  transition-property: color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .footer .footer-widget a {
    text-align: left;
  }
}
.footer .footer-widget a:hover {
  color: #84d9bc;
}
.footer .footer-widget a:last-of-type {
  margin-bottom: 0;
}
.footer .copyright {
  text-align: center;
  color: #ffffff;
  margin: 0 auto;
  border-top: 2px solid #84d9bc;
  padding: 1rem;
  max-width: 120rem;
  font-size: 1.2rem;
  text-transform: uppercase;
}

.button-primary, .button-plan {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.button-primary:hover, .button-plan:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .button-primary, .button-plan {
    width: auto;
    display: inline-block;
  }
}

.button-plan {
  border: 0.2rem solid #ffffff;
  margin-top: 2rem;
  display: block;
}